form input {
    width: 100%;
    text-transform: lowercase;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
    outline: none;
}

form textarea {
    width: 100%;
    height: 60px;
    resize: vertical;
    min-height: 140px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
}

section.dropdreg .content-drop input {
    background: $First !important;
}

.button {
    width: 100%;
    height: 60px;
    background: $Second;
    border: 0;
    border-radius: 8px;
    padding: 0 24px;
    color: #fffbfbfb;
    outline: none !important;
    font-weight: 700;
    margin-top: 16px;

    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: filter 0.6s;
}

.button:hover {
    filter: brightness(90%);
}

.back-link {

    display: flex;
    align-items: center;
    height: 50px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    margin-top: 16px;
    font-weight: 500;
    transition: opacity 0.2s;

    span {
        display: flex;
        height: 100%;
        align-items: center;
        margin-right: 16px;


        img {

            width: 20px !important;
        }
    }

    p {
        height: 100%;
        margin: 0;
        padding: 0;
        align-items: center;

        display: flex;
    }

}



.back-link {
    opacity: 0.8;
}